body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #282c34;
}

nav {
  background-color: #282c34;
  padding: 10px;
}

nav a {
  color: white;
  margin: 0 10px;
  text-decoration: none;
}

nav a:hover {
  text-decoration: underline;
}

.page {
  padding: 20px;
  color: white;
}

.dashboard {
  padding: 20px;
  border: 2px solid #61dafb;
  border-radius: 8px;
  background-color: #282c34;
  max-width: 600px;
  margin: 20px auto;
}

.upload-section {
  margin: 20px 0;
}

.results-section {
  margin-top: 20px;
}

.notification {
  margin-top: 20px;
  padding: 10px;
  background-color: #ffeb3b;
  color: #000;
  border-radius: 5px;
}

button {
  padding: 10px 20px;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background-color: #888;
  cursor: not-allowed;
}

/*home page*/
.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #282c34;
  color: white;
}

.App-header {
  padding: 20px;
  border: 2px solid #61dafb;
  border-radius: 10px;
  background-color: #20232a;
}

.home-page button{
  margin: 20px;
}